import React from 'react';

function CommentsList(props) {
  return (
    <>
      <h3>Comments</h3>
      {props.comments.map((comment, key) => {
        return (
          <div key={key}>
            <h4>{comment.username}</h4>
            <p>{comment.text}</p>
          </div>
        );
      })}
    </>
  );
}

export default CommentsList;
