import React, { useState, useEffect } from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';

function Article(props) {
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
  const name = props.match.params.name;
  const article = articleContent.find((article) => article.name === name);
  const otherArticles = articleContent.filter(
    (article) => article.name !== name
  );

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
  }, [name]);

  if (!article) {
    return <NotFoundPage />;
  }

  return (
    <div className=" bg-green-100 border-gray-200 border-2 rounded-md px-8 py-4 shadow-lg">
      <h1 className="text-5xl font-bold pt-8 pb-4 text-gray-700 ">
        {article.title}
      </h1>
      <UpvotesSection
        articleInfo={articleInfo}
        name={name}
        setArticleInfo={setArticleInfo}
      />
      {article.content.map((paragraph, key) => (
        <p key={key} className="font-normal py-2 text-gray-600">
          {paragraph}
        </p>
      ))}
      <CommentsList comments={articleInfo.comments} />
      <AddCommentForm name={name} setArticleInfo={setArticleInfo} />
      <h2 className="ml-4 mt-6 mb-2 text-3xl font-medium">Other Articles:</h2>
      <ArticlesList articles={otherArticles} />
    </div>
  );
}

export default Article;
