import React from 'react';

const UpvotesSection = (props) => {
  const handleClick = async () => {
    const result = await fetch(`/api/articles/${props.name}/upvote`, {
      method: 'POST',
    });
    const body = await result.json();
    props.setArticleInfo(body);
  };

  return (
    <div>
      <button onClick={handleClick}>Add Upvote</button>
      <p>This post has been upvoted {props.articleInfo.upvotes} times.</p>
    </div>
  );
};

export default UpvotesSection;
